import Utilities from '../common/utilities';
import BrowserHandler from '../common/browserHandler';
import angular from 'angular';
import constants from '../common/constants';
import {checkPermission} from '../common/permissions';

export default angular
    .module('app.background', [])
    .controller('BackgroundController', [
        '$window',
        '$http',
        '$q',
        '$log',
        'notification',
        'storage',
        'userService',
        'fileService',
        'copyPasteService',
        backgroundController,
    ]);

function backgroundController($window, $http, $q, $log, notification, storage, userService, fileService, copyPasteService) {
    userService.isLoggedIn().then(status => {
        status ? Utilities.setIcon(constants.BROWSER_ICON.DEFAULT) : Utilities.setIcon(constants.BROWSER_ICON.INACTIVE);
    });

    var webRedirectDisabled;
    storage.get('webRedirect_disabled').then(function (result) {
        webRedirectDisabled = Utilities.isNotUndefinedOrNull(result.webRedirect_disabled) && result.webRedirect_disabled;
    });

    BrowserHandler.windows.onFocusChanged.addListener(function (windowId) {
        updateSessionInfo();
    });

    var sessionInfo;

    async function updateSessionInfo(force) {
        var currentTime = new Date();

        if (sessionInfo) {
            var startSession = new Date(sessionInfo.startTime);
            // update the elapsed time
            var elapsed = currentTime - startSession;
            sessionInfo.duration += elapsed;
        } else {
            var result = await storage.get('LastSession');
            if (result && result.LastSession && Number.isInteger(result.LastSession.duration) && Date.parse(result.LastSession.startTime)) {
                sessionInfo = result.LastSession;

                // remap old start_time to new startTime
                if (sessionInfo.hasOwnProperty('start_time')) {
                    sessionInfo.startTime = sessionInfo.start_time;
                    delete sessionInfo.start_time;
                }
            } else {
                sessionInfo = {
                    startTime: currentTime,
                    duration: 0,
                };
            }
        }

        // if the currentTime is not the same day as the last update,
        // log the telemetry and reset the start time
        var logDay = new Date(sessionInfo.startTime);
        var today = new Date(currentTime);
        today.setHours(0, 0, 0, 0);
        logDay.setHours(0, 0, 0, 0);

        if (force || today - logDay) {
            $log.trackEvent('Session', {startTime: sessionInfo.startTime, duration: sessionInfo.duration, minutes: Math.floor(sessionInfo.duration / 60000)});
            sessionInfo.duration = 0;
        }

        sessionInfo.startTime = currentTime.toString();

        storage.set({
            LastSession: sessionInfo,
        });
    }

    BrowserHandler.storage.onChanged.addListener(
        function (changes, areaName) {
            if (Utilities.isNotUndefinedOrNull(changes.webRedirect_disabled)) {
                webRedirectDisabled = changes.webRedirect_disabled.newValue;
            }
            else if (changes.identity) {
                var identity = changes.identity.newValue;
                identity ? Utilities.setIcon(constants.BROWSER_ICON.DEFAULT) : Utilities.setIcon(constants.BROWSER_ICON.INACTIVE);
            }
        });

    // Begin documents from the web
    var documentTabId;

    BrowserHandler.webRequest.onSendHeaders.addListener(
        function (data) {
            // Only evaluate web requests where the resource is the document of a tab content window
            if (data.type !== 'main_frame') {
                return;
            }
            // Examine Referer header to determine whether to redirect to Office for the web or not.
            for (var i = 0; i < data.requestHeaders.length; ++i) {
                if (data.requestHeaders[i].name === 'Referer') {
                    if (data.requestHeaders[i].value.indexOf('officeapps.live.com') >= 0) {
                        // If user is viewing document in Office on the web, set the tabId to null to stop redirecting.
                        documentTabId = null;
                    } else {
                        documentTabId = data.tabId;
                    }
                    return;
                }
            }
        },
        {
            urls: [
                '*://*/*.doc',
                '*://*/*.doct',
                '*://*/*.docx',
                '*://*/*.dotx',
                '*://*/*.odt',
                '*://*/*.ppt',
                '*://*/*.pot',
                '*://*/*.pps',
                '*://*/*.pptx',
                '*://*/*.ppsx',
                '*://*/*.odp',
                '*://*/*.xls',
                '*://*/*.xlsx',
                '*://*/*.xlsm',
                '*://*/*.xlsb',
                '*://*/*.ods',
            ],
        },
        ['requestHeaders']
    );

    BrowserHandler.webRequest.onHeadersReceived.addListener(
        function (data) {
            // If web document redirecting settings has been disabled by the user, stop redirecting.
            if (webRedirectDisabled) {
                return;
            }

            // Only evaluate web requests where the resource is the document of a tab content window
            if (data.type !== 'main_frame') {
                return;
            }

            // Continue processing this request iff we are able to inspect the response headers, the referrer was not Office for the web (ie documentTabId === null)
            if (
                Utilities.isUndefinedOrNull(data.responseHeaders) ||
                Utilities.isUndefinedOrNull(documentTabId) ||
                Utilities.isUndefinedOrNull(data.tabId) ||
                documentTabId !== data.tabId
            ) {
                return;
            }

            // Examine Cache-Control and Content-Type in response header.
            // Redirect valid online document to be viewed in Office on the web.
            var response = {};
            for (var i = 0; i < data.responseHeaders.length; ++i) {
                response[data.responseHeaders[i].name] = data.responseHeaders[i].value;
            }

            if (isResponseHeaderPrivate(response, 'Cache-Control') || isResponseHeaderPrivate(response, 'Pragma')) {
                // If the web linked document is a private content, stop redirecting.
                return;
            } else if (response['Content-Type']) {
                if (constants.FILE.OFFICE_MIME_TYPES[response['Content-Type'].toLowerCase()]) {
                    // Check if the web linked document is a valid Office document
                    $log.trackEvent('OpenDocument_BrowserWebDocument');

                    storage.get('displayWebDocRedirectNotification').then(function (result) {
                        if (
                            Utilities.isUndefinedOrNull(result.displayWebDocRedirectNotification) ||
                            result.displayWebDocRedirectNotification === true
                        ) {
                            notification.show(constants.NOTIFICATION.WEBDOCREDIRECT);
                        }
                    });

                    return {redirectUrl: 'https://view.officeapps.live.com/op/view.aspx?src=' + data.url};
                } else {
                    $log.trackEvent('WebRequest_Header', {'contentType': response['Content-Type']});
                    return;
                }
            }
            return;
        },
        {
            urls: [
                '*://*/*.doc',
                '*://*/*.doct',
                '*://*/*.docx',
                '*://*/*.dotx',
                '*://*/*.odt',
                '*://*/*.ppt',
                '*://*/*.pot',
                '*://*/*.pps',
                '*://*/*.pptx',
                '*://*/*.ppsx',
                '*://*/*.odp',
                '*://*/*.xls',
                '*://*/*.xlsx',
                '*://*/*.xlsm',
                '*://*/*.xlsb',
                '*://*/*.ods',
            ],
        },
        ['responseHeaders', 'blocking']
    );

    // End documents from the web

    // Begin documents from local storage

    // Add a listener for local file web requests
    // Fires when dragging a file into the browser or using the file picker
    BrowserHandler.webRequest.onBeforeRequest.addListener(
        function (data) {
            // Only evaluate web requests where the resource is the document of a tab content window
            if (data.type !== 'main_frame') {
                return;
            }
            $log.trackEvent('OpenDocument_BrowserDragAndDrop');
            var activeTabId = data.tabId;
            fileService.upload(data.url, constants.FILE.ORIGIN.LOCAL_PATH, function (fileUrl) {
                if (Utilities.isUndefinedOrNull(fileUrl)) {
                    // File upload failed; return
                    return;
                }

                openFileUrlInTab(fileUrl, activeTabId);
            });

            return {redirectUrl: constants.LINKS.PROGRESSPAGE_URL};
        },
        {
            urls: [
                'file:///*.doc',
                'file:///*.doct',
                'file:///*.docx',
                'file:///*.dotx',
                'file:///*.odt',
                'file:///*.ppt',
                'file:///*.pot',
                'file:///*.pps',
                'file:///*.pptx',
                'file:///*.ppsx',
                'file:///*.odp',
                'file:///*.xlsx',
                'file:///*.xlsm',
                'file:///*.xlsb',
                'file:///*.ods',
            ],
        },
        ['blocking']
    );

    // Listening for errors in local file web requests.
    BrowserHandler.webRequest.onErrorOccurred.addListener(
        function (details) {
            // Record the error
            $log.error(`BackgroundWebRequestOnErrorOccured - ${details.error}`);
        },
        {
            urls: [
                'file:///*.doc',
                'file:///*.doct',
                'file:///*.docx',
                'file:///*.dotx',
                'file:///*.odt',
                'file:///*.ppt',
                'file:///*.pot',
                'file:///*.pps',
                'file:///*.pptx',
                'file:///*.ppsx',
                'file:///*.odp',
                'file:///*.xlsx',
                'file:///*.xlsm',
                'file:///*.xlsb',
                'file:///*.ods',
            ],
        }
    );

    // End documents from local storage

    // Display log install or update and clear old data
    BrowserHandler.runtime.onInstalled.addListener(async function (details) {
        var manifest = Utilities.getManifest();
        var appVersion = manifest ? manifest.version : -1;

        if (details.reason === 'install') {
            $log.trackEvent('AppInstalled', {'appVersion': appVersion});

            // I don't think this is a thing anymore, I don't see it in the extension settings
            // and the manifest supports permissions now
            // Prompt allow access to file urls notification when first time installing the app
            // notification.show(constants.NOTIFICATION.FILEACCESS);

            BrowserHandler.runtime.getPlatformInfo(platformInfo => {
                if (platformInfo.os === 'cros') {
                    // Prompt double click to open notification when first time installing the app on ChromeOS
                    notification.show(constants.NOTIFICATION.SETDEFAULT);
                }
            });

            // Show support page when when first time installing the app
            // Editor specific
            var supportUrl = Utilities.shouldShowEditorMaterial() ? constants.LINKS.SUPPORT_URL_EDITOR : constants.LINKS.SUPPORT_URL;

            // There have been complaints of this showing after updating so look for some residual
            // data in local storage to see if this is really a first install before opening the supportUrl
            try {
                const result = await storage.get(['deviceId', 'LastSession']);
                // if there are any values (keys) then it's not really a first install
                if (Object.keys(result).length) {
                    return;
                }
            }
            catch (e) {
                // if an exception was thrown trying to retrieve any settings then assume
                // this is first run
            }
            openFileUrlInTab(supportUrl, null);

        }
        else if (details.reason === 'update') {
            $log.trackEvent('AppUpdated', {'appVersion': appVersion, previousVersion: details.previousVersion});

            if (Utilities.isNotUndefinedOrNull(window.localStorage.getItem(constants.OAUTH.SERVICE_ID))) {
                // If the serviceInfo is stored in window.localStorage, reset the storage data.
                storage.clear();
                window.localStorage.clear();
            }

            storage.get('userInfo').then(function (result) {
                if (
                    Utilities.isNotUndefinedOrNull(result) &&
                    Utilities.isNotUndefinedOrNull(result.userInfo) &&
                    (Utilities.isNotUndefinedOrNull(result.userInfo.id) || Utilities.isNotUndefinedOrNull(result.userInfo.profile))
                ) {
                    // If the userInfo is in old data form, reset the storage data.
                    storage.clear();
                    window.localStorage.clear();
                }
            });
        }
    });

    BrowserHandler.notifications.onButtonClicked.addListener(function (notificationId) {
        switch (notificationId) {
            case constants.NOTIFICATION.AUTOSAVE.id:
                storage.set({displayAutoSaveNotification: false});
                break;
            case constants.NOTIFICATION.WEBDOCREDIRECT.id:
                storage.set({displayWebDocRedirectNotification: false});
                break;
        }
    });

    BrowserHandler.notifications.onClicked.addListener(function (notificationId) {
        switch (notificationId) {
            case constants.NOTIFICATION.FILEACCESS.id:
                BrowserHandler.tabs.create({url: constants.LINKS.SETTINGS_URL});
                break;
            case constants.NOTIFICATION.INVALIDSUBSCRIPTION.id:
                BrowserHandler.tabs.create({url: constants.LINKS.OFFICE_URL});
                break;
        }
    });

    // Listen to incoming messages from foreground pages process requests
    BrowserHandler.runtime.onMessage.addListener(function (request, sender, sendResponse) {
        switch (request.eventId) {
            // ignoring messages from editor telemetry with known eventIds
            case "telemetryEvent":
            case "newActivityTelemetryEvent":
                return;
        }

        // ignore messages that don't have activity as they aren't ours
        if (!request.activity) {
            return;
        }

        switch (request.activity) {
            case constants.ACTIVITY.LOG.NAME:
                {
                    var source = undefined;
                    if (sender.url) {
                        var url = new URL(sender.url);
                        source = url.origin + url.pathname;
                    }

                    $log[request.severity.toLowerCase()]({message: request.log, stack: request.stack, filename: request.filename, source});
                    // no need to log (below switch block) that we've logged so just return
                    return;
                }
            case constants.ACTIVITY.SSO.NAME:
                {
                    // Handle SSO for AAD users by sending messages to native client
                    try {
                        // Pass the sender into the native host to validate that the page is able to call this method.
                        request.body.sender = sender.origin;

                        if ('GetSupportedUrls'.localeCompare(request.body.method, undefined, { sensitivity: 'base' }) == 0) {
                            RespondWithError(CreateInvalidMethodResponse(), sendResponse);
                            return true;
                        }

                        BrowserHandler.runtime.sendNativeMessage(
                            constants.SSO.MESSAGE_CHANNEL,
                            request.body,
                            function (result) {
                                var response = {};
                                $log.debug(`BrowserCore response: ${JSON.stringify(result)}`);
                                if (result) {
                                    if (result.status === constants.SSO.RESPONSE_STATUS_FAIL) {
                                        RespondWithError(result, sendResponse);
                                    } else {
                                        response = Object.assign({status: constants.SSO.RESPONSE_STATUS_SUCCESS, result});
                                        $log.trackEvent('SSO_SUCCESS');
                                        sendResponse(response);
                                    }
                                } else {
                                    RespondWithError({
                                        status: constants.SSO.RESPONSE_STATUS_FAIL,
                                        code: constants.SSO.RESPONSE_CODE_NO_SUPPORT,
                                        description: BrowserHandler.runtime.lastError.message,
                                    }, sendResponse);
                                }
                            });
                    }
                    catch (e) {
                        RespondWithError({
                            status: constants.SSO.RESPONSE_STATUS_FAIL,
                            code: constants.SSO.RESPONSE_CODE_NO_SUPPORT,
                            description: e.toString(),
                        }, sendResponse);
                    }

                    return true;
                }
            case constants.ACTIVITY.TELEMETRY.NAME:
                switch (request.command) {
                    case constants.TELEMETRY.COMMAND.SET_DISABLED:
                        $log.setEnabledSetting(request.enabled);
                        setUninstallUrl(request.enabled);
                        break;
                    case constants.TELEMETRY.COMMAND.TRACK_EVENT:
                        $log.trackEvent(request.eventName, request.properties, request.measurements);
                        break;
                    case constants.TELEMETRY.COMMAND.TRACK_TRACE:
                        $log.trackTrace(request.message, request.properties);
                        break;
                }
                break;
            case constants.ACTIVITY.NOTIFICATION.NAME:
                notification.show(request.notification);
                break;
            case constants.ACTIVITY.AUTHENTICATION.NAME:
            case constants.ACTIVITY.AUTHORIZATION.NAME:
            case constants.ACTIVITY.LOGOUT.NAME:
            case constants.ACTIVITY.REQUEST_TOKEN.NAME:
                // Ignore these that are explicitly handled elsewhere
                return;
            default: {
                $log.warn(`Unhandled activity:${JSON.stringify(request)}`);
                return;
            }
        }

        $log.debug(`BackgroundController.onMessage: Processed activity ${request.activity}`);
    });

    // We only support copy&paste in Chrome, so check browser and register listeners if we are running on Chrome.
    if (Utilities.isChrome()) {
        checkPermission(['clipboardRead'], $log).then(granted => {
            if (!granted) {
                return;
            }
            BrowserHandler.runtime.onMessageExternal.addListener(function (msg, sender, callback) {
                switch (msg.command) {
                    case constants.COPY_PASTE.TEST:
                        return copyPasteService.handleTestCommand(msg, sender, callback);
                    case constants.COPY_PASTE.PASTE:
                        return copyPasteService.handlePasteCommand(msg, sender, callback);
                    case constants.COPY_PASTE.GET_CLIPBOARD_DATA:
                        return copyPasteService.handleGetClipboardDataCommand(msg, sender, callback);
                    case constants.COPY_PASTE.GET_AVAILABLE_COMMANDS:
                        return copyPasteService.handleGetAvailableCommands(msg, sender, callback);
                    default:
                        return false;
                }
            });
        });
        $window.addEventListener('paste', copyPasteService.onPasteEvent);

        // SSO for gsuite account federated with AAD on Chrome OS
        checkPermission(['identity', 'identity.email'], $log).then(granted => {
            if (!granted) {
                return;
            }

            BrowserHandler.tabs.onUpdated.addListener(function (tabId, changeInfo, tab) {
                if (
                    !changeInfo.url ||
                    !isLogInUrl(changeInfo.url) ||
                    changeInfo.url.indexOf('login_hint') !== -1 ||
                    changeInfo.url.indexOf('username') !== -1
                ) {
                    return;
                }

                BrowserHandler.identity.getProfileUserInfo(function (userInfo) {
                    if (!userInfo || !userInfo.email) {
                        return;
                    }
                    var email = userInfo.email;

                    var request = {
                        method: 'GET',
                        url: constants.O365CONFIG.FEDERATED_URL + '?login=' + email,
                    };

                    $http(request).then(
                        function (response) {
                            var data = response.data;
                            if (data && data.NameSpaceType === 'Federated' && data.DomainName !== 'live.com') {
                                var param = changeInfo.url.indexOf('https://login.live.com/login.srf') === -1 ? 'login_hint' : 'username';
                                var currentUrl = changeInfo.url;
                                if (currentUrl.indexOf('?') !== -1) {
                                    currentUrl += '&' + param + '=' + email;
                                } else {
                                    currentUrl += '?' + param + '=' + email;
                                }
                                BrowserHandler.tabs.update(tabId, {url: currentUrl});
                            }
                        },
                        function () {
                            return;
                        });
                });
            });
        });
    }

    BrowserHandler.runtime.getPlatformInfo(function (platformInfo) {
        if (platformInfo.os !== 'cros') {
            return;
        }

        checkPermission('fileBrowserHandler', $log).then(granted => {
            if (!granted) {
                return;
            }

            // Listen to incoming messages from file browser handler for Chrome OS.
            BrowserHandler.fileBrowserHandler.onExecute.addListener(function (id, details) {
                var fileEntries = details.entries;
                uploadFiles(fileEntries, function (fileUrl) {
                    if (Utilities.isUndefinedOrNull(fileUrl)) {
                        // File upload failed; return
                        return;
                    }

                    openFileUrlInTab(fileUrl, null);
                });
            });
        });
    });

    function isLogInUrl(url) {
        return (
            url.search(/^https:\/\/login\.microsoftonline\.com\/(common|.{8}-.{4}-.{4}-.{4}-.{12})\/oauth2\/authorize/) != -1 ||
            url.indexOf('https://login.live.com/login.srf') !== -1
        );
    }

    function receiveMessage(event) {
        if (BrowserHandler.runtime.getURL('').search(new RegExp(event.origin, 'i')) !== 0) {
            $log.error(`BackgroundController.receiveMessage: Error processing message with unrecognized origin ${event.origin}`);
            return;
        }

        if (Object.prototype.toString.call(event.data) !== '[object File]') {
            $log.error(
                `BackgroundController.receiveMessage: Error processing message with event.data type ${Object.prototype.toString.call(event.data)}`
            );
            return;
        }

        uploadFile(event.data, function (fileUrl) {
            if (Utilities.isUndefinedOrNull(fileUrl)) {
                // File upload failed; return
                return;
            }

            openFileUrlInTab(fileUrl, null);
        });
    }
    window.addEventListener('message', receiveMessage, false);

    // Creates a new browser tab with progresspage url and returns its' tab id
    function getDisplayTabId(startUrl) {
        if (Utilities.isUndefinedOrNull(startUrl)) {
            startUrl = constants.LINKS.PROGRESSPAGE_URL;
        }

        var deferred = $q.defer();
        Utilities.safeOpenNewTab(startUrl, true, function(tab) {
            deferred.resolve(tab.id);
        });

        return deferred.promise;
    }

    // Helper method that takes an array of HTML5 file entries and uploads each file; executes callback after each upload
    function uploadFiles(fileEntries, callback) {
        if (Utilities.isUndefinedOrNull(fileEntries)) {
            callback(null);
            return;
        }

        for (var i = 0; i < fileEntries.length; ++i) {
            fileEntries[i].file(function (file) {
                uploadFile(file, function (fileUrl) {
                    callback(fileUrl);
                });
            });
        }
    }

    // Helper method that takes an  HTML5 file entry and uploads the file; executes callback after upload
    function uploadFile(file, callback) {
        if (Utilities.isUndefinedOrNull(file)) {
            callback(null);
            return;
        }

        fileService.upload(file, constants.FILE.ORIGIN.HTML5, function (fileUrl) {
            callback(fileUrl);
        });
    }

    // Helper method that opens a file url given a tab id; will create a new window or tab if tabId is null
    function openFileUrlInTab(fileUrl, tabId) {
        var deferred = $q.defer();
        deferred.resolve(Utilities.isNotUndefinedOrNull(tabId) ? tabId : getDisplayTabId(fileUrl));

        deferred.promise.then(function (tabId) {
            if (Utilities.isUndefinedOrNull(tabId)) {
                $log.warn('BackgroundController.openFileUrlInTab: tabIdPromise null or undefined');
                return;
            }

            BrowserHandler.tabs.update(tabId, {url: fileUrl});
        });
    }

    function isResponseHeaderPrivate(response, header) {
        return response[header] && response[header].toLowerCase().indexOf('private') !== -1;
    }

    function initialize() {
        $log.getEnabledSetting().then(function (enabled) {
            setUninstallUrl(enabled);
        });

    }

    function setUninstallUrl(enabled) {
        if (!Utilities.isNotUndefinedOrNull(enabled)) {
            enabled = true;
        }

        // Check if setUninstallURL is enabled
        if (Utilities.isNotUndefinedOrNull(BrowserHandler.runtime.setUninstallURL)) {
            var manifest = Utilities.getManifest();
            var appVersion = manifest ? manifest.version : -1;

            // Use office.com as default uninstall page
            var url = Utilities.isExtensionInDevelopmentMode() ? constants.LINKS.OFFICE_HOME_DEV_URL : constants.LINKS.OFFICE_HOME_URL;

            // Check if logging is enabled by user
            if (enabled) {
                url = constants.LINKS.UNINSTALL + '?version=' + appVersion;
                url = Utilities.isExtensionInDevelopmentMode() ? url + '&isDebug=true' : url;
                // Editor specific
                url = Utilities.shouldShowEditorMaterial() ? (url + '&shouldShowEditorMaterial=true') : url;
            }

            BrowserHandler.runtime.setUninstallURL(url);
        }
    }

    function RespondWithError(response, sendResponse) {
        if (IsInvalidMethod(response)) {
            // for Invalid Method we want produce the same response regardless it came from extension or native code, it should be hidden from caller.
            const invalidResponse = CreateInvalidMethodResponse();
            $log.trackEvent('SSO_FAIL', invalidResponse);
            sendResponse(invalidResponse);
        }
        else {
            $log.trackEvent('SSO_FAIL', response);
            sendResponse(response);
        }
    }

    function IsInvalidMethod(response) {
        return response.ext && response.ext.error === constants.SSO.RESPONSE_CODE_INVALID;
    }

    function CreateInvalidMethodResponse() {
        return {
            status: 'Fail',
            code: 'OSError',
            description: 'Error processing request.',
            ext: { error: constants.SSO.RESPONSE_CODE_INVALID }
        };
    }

    initialize();
}
