import Utilities from '../common/utilities';
import constants from '../common/constants';

export default function sharePointFileService($http, $log/* , constants */, localize, userService) {

    function renameFile(fileName) {
        var now = new Date().toISOString();
        var reg = /(\d+)-(\d+)-(\d+)T(\d+):(\d+):(\d+).+/;
        var version = now.replace(reg, '_$1$2$3_$4$5$6.');
        return fileName.replace('.', version);
    }

    this.doUpload = async function (file, callback) {
        file.name = renameFile(file.name);
        var userInfo = await userService.getUserInfo(constants.USER_TYPE.O365);
        if (Utilities.isUndefinedOrNull(userInfo) || Utilities.isUndefinedOrNull(userInfo.oneDriveUrl)) {
            $log.error('sharePointFileService.doUpload - Invalid OneDriveUrl');
            callback(null);
            return;
        }

        var requestUrl = constants.O365CONFIG.UPLOAD_URL +
            encodeURIComponent(localize.getLocalizedString('AppFolderName')) +
            '/' + file.name + ':/content';

        var request = {
            method: 'PUT',
            url: requestUrl,
            headers: {
                'Content-Type': 'application/octet-stream',
                'X-UserType': constants.USER_TYPE.O365
            },
            data: file.content,
            transformRequest: []
        };

        $http(request).then(
            function (response) {
                var data = response.data;
                var status = response.status;
                var etag = '', fileUrl = '';

                if (Utilities.isUndefinedOrNull(data.eTag)) {
                    $log.error('sharePointFileService.doUpload - Missing eTag');
                    callback(null, data, status);
                    return;
                }
                etag = data.eTag.substring(1, data.eTag.indexOf(','));

                if (Utilities.isUndefinedOrNull(data['@microsoft.graph.downloadUrl'])) {
                    $log.error('sharePointFileService.doUpload - Missing downloadUrl');
                    callback(null, data, status);
                    return;
                }
                fileUrl = data['@microsoft.graph.downloadUrl'].replace(/download.aspx?.+/, 'WopiFrame.aspx?sourcedoc=' + etag);

                $log.debug(`sharePointFileService.doUpload - ${status} - ${fileUrl}`);
                callback(fileUrl);
            },
            function (response) {
                callback(null, response.data, response.status);
            });
    };
}
