import angular from 'angular';
import { CopyPasteService, FileReaderEx, ImageReader } from '@1js/office-online-ccp';

export default angular
    .module('app.copypaste', [])
    .factory('copyPasteService', ['$log', 'imageReader', '$window', ($log, imageReader, $window) => new CopyPasteService($log, imageReader, $window)])
    .factory('fileReader', () => new FileReaderEx())
    .factory('imageReader', ['$log', 'fileReader', ($log, fileReader) => new ImageReader($log, fileReader)])
    .name;
