import Utilities from '../common/utilities';
import constants from '../common/constants';

export default function fileService($http, $q, $log, localize, notification, storage, fileLoader, userService, sharePointFileService, oneDriveFileService) {
    var currentUserType = constants.USER_TYPE.NONE;

    function handleFileLoadError(error) {
        if (Utilities.isUndefinedOrNull(error)) {
            return;
        }

        switch (error) {
            case (constants.FILE.ERROR.FILE_SIZE_MAX_EXCEEDED):
                notification.show(constants.NOTIFICATION.FILE_MAX_SIZE_BYTE_LIMIT_REACHED);
                break;
            case (constants.FILE.ERROR.UNSUPPORTED_FILE_TYPE):
                notification.show(constants.NOTIFICATION.UNSUPPORTEDFILETYPE);
                break;
            default:
                $log.error(`fileService.loadFile - ${error}`);
                notification.show(constants.NOTIFICATION.FILE_UPLOAD_FAILURE_GENERIC);
        }
    }

    function handleFileUploadError(data, status) {
        if (Utilities.isUndefinedOrNull(status)) {
            return;
        }

        if (status === 415) {
            notification.show(constants.NOTIFICATION.FILE_UPLOAD_FAILURE_UNSUPPORTED_MEDIA);
        }
        else if (status === 502 || status === 503 || status === 509) {
            notification.show(constants.NOTIFICATION.FILE_UPLOAD_FAILURE_SERVER);
        }
        else if (data) {
            $log.error(`fileService.doUpload - ${status} - ${JSON.stringify(data)}`);
            notification.show(constants.NOTIFICATION.FILE_UPLOAD_FAILURE_GENERIC);
        }
    }

    function getFileServiceProvider(userType) {
        var fileServiceProvider = null;

        // Determine which file service to use and inject
        switch (userType) {
            case constants.USER_TYPE.O365:
                fileServiceProvider = sharePointFileService;
                break;
            case constants.USER_TYPE.MSA:
                fileServiceProvider = oneDriveFileService;
                break;
            default:
                notification.show(constants.NOTIFICATION.NOTSIGNEDIN);
                $log.trackEvent('FileUserTypeError_UserNotSignedIn');
                break;
        }

        return fileServiceProvider;
    }

    function doUpload(file, callback) {
        var fileServiceProvider;

        userService.getUserType().then(function (userType) {
            currentUserType = userType;
            fileServiceProvider = getFileServiceProvider(userType);
            if (Utilities.isUndefinedOrNull(fileServiceProvider)) {
                callback(null);
                return;
            }

            // Show a notification that file upload is now in progress
            $log.trackEvent('FileServiceProviderUploadDocType', {'extension': Utilities.getFileExtension(file.name)});
            notification.show(constants.NOTIFICATION.FILE_UPLOAD_IN_PROGRESS);

            fileServiceProvider.doUpload(file, function (fileUrl, data, status) {
                if (Utilities.isUndefinedOrNull(fileUrl)) {
                    handleFileUploadError(data, status);
                }

                callback(fileUrl);
            });
        });
    }

    function upload(fileInfo, fileOrigin, callback) {
        if (Utilities.isUndefinedOrNull(fileInfo) || (fileOrigin !== constants.FILE.ORIGIN.HTML5 && fileOrigin !== constants.FILE.ORIGIN.LOCAL_PATH)) {
            $log.error(`fileService.upload(): Invalid argument: fileInfo: ${JSON.stringify(fileInfo)}, fileOrigin: ${fileOrigin}`);
            callback(null);
            return;
        }

        fileLoader.loadFile(fileInfo, fileOrigin).then(
            function (file) {
                doUpload(file, function (fileUrl) {
                    if (Utilities.isUndefinedOrNull(fileUrl)) {
                        callback(null);
                        return;
                    }

                    storage.get('displayAutoSaveNotification').then(function (result) {
                        if (Utilities.isUndefinedOrNull(result.displayAutoSaveNotification) || result.displayAutoSaveNotification === true) {
                            var autoSaveNotification = constants.NOTIFICATION.AUTOSAVE;
                            autoSaveNotification.message = currentUserType === constants.USER_TYPE.MSA ? constants.NOTIFICATION.AUTOSAVETOONEDRIVE : constants.NOTIFICATION.AUTOSAVETOSHAREPOINT;
                            notification.show(autoSaveNotification);
                        }

                        callback(fileUrl);
                    });
                });
            },
            function (error) {
                handleFileLoadError(error);
                callback(null);
                return;
            }
        );
    }

    return {upload};
}
