import BrowserHandler from '../common/browserHandler';

('use strict');

function promised(proc: any): any {
    return function (...args: ((err: any, ...results: unknown[]) => void)[]): Promise<any> {
        return new Promise((resolve, reject) => {
            function callback(...results: unknown[]): void {
                return resolve(...results);
            }

            args.push(callback);
            proc.call(this, ...args);
        });
    };
}

async function filterNeeded(permissions: string | string[]): Promise<string[]> {
    if (!Array.isArray(permissions)) {
        permissions = [permissions];
    }

    var granted = await promised(BrowserHandler.permissions.getAll)();
    console.debug(`permissions granted: ${JSON.stringify(granted)}`);

    return permissions.filter((permission: string) => {
        return !granted.permissions.includes(permission);
    });
}

export function checkPermission(requested: string | string[], log: { trackEvent: Function } | null = null): Promise<boolean> {
    if (!Array.isArray(requested)) {
        requested = [requested];
    }

    return new Promise(async (resolve, reject) => {
        try {
            var permissions = await filterNeeded(requested);
            if (!permissions.length) {
                return resolve(true);
            }

            var result = await promised(BrowserHandler.permissions.contains)({ permissions });
            console.debug(`Granted permissions ${result === true ? '' : 'do not'} include ${permissions}`);
            if (result) {
                return resolve(result);
            }
            var granted = await promised(BrowserHandler.permissions.request)({ permissions });

            console.debug(`Requested permissions ${permissions} ${granted ? 'granted' : 'denied'}`);
            var eventProps: any = {
                granted: !!granted,
                permissions,
            };

            if (!granted && BrowserHandler.runtime) {
                eventProps.lastError = BrowserHandler.runtime.lastError?.message;
                eventProps.id = BrowserHandler.runtime.id;
            }

            log?.trackEvent('Permission', eventProps);
            return resolve(!!granted);
        } catch (err) {
            resolve(false);
        }
    });
}

export async function preemptivePermissionsRequest(): Promise<void> {
    try {
        var optional = BrowserHandler.runtime.getManifest().optional_permissions;
        await checkPermission(optional);
    } catch (err) {
        console.log(`preemptivePermissionsRequest exception:${err}`);
    }
}
