import angular from 'angular';
import fileService from './file.service';
import fileLoader from './fileLoader.service';
import oneDriveFileService from './oneDriveFile.service';
import sharePointFileService from './sharePointFile.service';

export default angular
    .module('app.file', [])
    .factory('fileService', ['$http', '$q', '$log', 'localize', 'notification', 'storage', 'fileLoader', 'userService', 'sharePointFileService', 'oneDriveFileService', fileService])
    .factory('fileLoader', ['$http', '$q', '$log', fileLoader])
    .service('oneDriveFileService', ['$http', '$log', 'localize', oneDriveFileService])
    .service('sharePointFileService', ['$http', '$log', 'localize', 'userService', sharePointFileService])
    .name;
