import constants from '../common/constants';

export default function oneDriveFileService($http, $log, localize) {

    this.doUpload = function (file, callback) {
        var requestUrl = constants.ONEDRIVE.INSTANCE +
            '/drive/root:/' + encodeURIComponent(localize.getLocalizedString('AppFolderName')) +
            '/' + file.name + ':/content' + constants.ONEDRIVE.QUERY_OPTION;

        var request = {
            method: 'PUT',
            url: requestUrl,
            headers: {
                'Content-Type': 'application/octet-stream',
                'X-UserType': constants.USER_TYPE.MSA
            },
            data: file.content,
            transformRequest: []
        };

        $http(request).then(
            function (response) {
                var data = response.data;
                $log.debug(`oneDriveFileService.doUpload - ${response.status} - ${data.webUrl}`);
                callback(data.webUrl);
            },
            function (response) {
                callback(null, null, response.statusText);
            });
    };
}
