import Utilities from '../common/utilities';
import { AWTLogManager } from '@aria/webjs-sdk';

// Set instrumentation key depending on whether extension is in development mode
//[SuppressMessage("Microsoft.Security", "CS002:SecretInNextLine", Justification="not a auth secret. designates the Aria tenent")]
var tenantId = '2c8ba5f543dc45cabca6a8903ae65ec0-17b4b4b1-0680-4459-8c45-b7c284ec277d-7329';

// Initialize
AWTLogManager.initialize(tenantId);
const browserVersion = Utilities.getBrowserAndVersion();
if (browserVersion) {
    AWTLogManager.setContext('DeviceInfo.BrowserName', browserVersion.browser);
    AWTLogManager.setContext('DeviceInfo.BrowserVersion', browserVersion.version);
}
// Store the logger instance in a variable that is accessible in other methods of your page
var ariaLogger = AWTLogManager.getLogger(tenantId);
(window as any).ariaLogger = ariaLogger;
