import Utilities from '../common/utilities';
import constants from '../common/constants';

export default function fileLoader($http, $q, $log) {
    function loadFileFromPath(filePath) {
        var deferred = $q.defer();
        var xhr = new XMLHttpRequest();
        xhr.open('GET', filePath, true);
        xhr.responseType = 'arraybuffer';

        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4) {
                if (xhr.status === 200 || xhr.status === 0) {
                    var result = {};
                    result.content = xhr.response;
                    result.name = filePath.replace(/^.*[\\/]/, '');
                    deferred.resolve(result);
                } else {
                    $log.error(`fileLoader.loadFileFromPath(): XMLHttpRequestError at method loadFileFromPath - ${xhr.status}`);
                    deferred.reject(constants.FILE.ERROR.LOAD_FROM_PATH);
                    return deferred.promise;
                }
            }
        };

        xhr.send(null);

        return deferred.promise;
    }

    function loadFileFromHTML5(file) {
        var deferred = $q.defer();
        if (Utilities.isUndefinedOrNull(constants.FILE.SUPPORTED_FILE_TYPES[Utilities.getFileExtension(file.name)])) {
            deferred.reject(constants.FILE.ERROR.UNSUPPORTED_FILE_TYPE);
            return deferred.promise;
        }

        var reader = new FileReader();
        var result = {};
        result.name = encodeURI(file.name);

        reader.onload = function () {
            result.content = new Int8Array(reader.result);
            deferred.resolve(result);
        };

        reader.readAsArrayBuffer(file);

        return deferred.promise;
    }

    function loadFile(fileInfo, fileOrigin) {
        var deferred = $q.defer();

        if (
            Utilities.isUndefinedOrNull(fileInfo) ||
            (Utilities.isUndefinedOrNull(fileOrigin) && fileOrigin !== constants.FILE.ORIGIN.HTML5 && fileOrigin !== constants.FILE.ORIGIN.LOCAL_PATH)
        ) {
            deferred.reject(`fileLoader.loadFile(): Invalid argument: fileInfo: ${JSON.stringify(fileInfo)}, fileOrigin: ${fileOrigin}`);
            return deferred.promise;
        }

        if (fileOrigin === constants.FILE.ORIGIN.HTML5) {
            // Check size of file before attempting to load the file
            if (fileInfo.size > constants.FILE.MAX_SIZE_BYTE_LIMIT) {
                $log.info(`fileLoader.loadFile(): Attempt to upload file with size exceeding limit - ${fileInfo.size} bytes`);
                deferred.reject(constants.FILE.ERROR.FILE_SIZE_MAX_EXCEEDED);
                return deferred.promise;
            }

            loadFileFromHTML5(fileInfo).then(
                function (file) {
                    deferred.resolve(file);
                },
                function (error) {
                    deferred.reject(error);
                }
            );
        } else {
            loadFileFromPath(fileInfo).then(
                function (file) {
                    if (!file.content) {
                        $log.error(); (`fileLoader.loadFile(): loadFileFromPath failed`);
                        deferred.reject(constants.FILE.ERROR.LOAD_FROM_PATH);
                    }
                    // Check size of file after attempting to load the file
                    else if (file.content.byteLength > constants.FILE.MAX_SIZE_BYTE_LIMIT) {
                        $log.info(`fileLoader.loadFile(): Attempt to upload file with size exceeding limit - ${file.content.byteLength} bytes`);
                        deferred.reject(constants.FILE.ERROR.FILE_SIZE_MAX_EXCEEDED);
                    }
                    else {
                        deferred.resolve(file);
                    }
                },
                function (error) {
                    deferred.reject(error);
                }
            );
        }

        return deferred.promise;
    }

    var service = {
        loadFile: loadFile,
    };

    return service;
}
